.notfound {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  &__title {
    font-size: 90px;
    font-weight: bold;
    margin: 0;
  }

  &__subtitle {
    font-size: 34px;
  }

  &__image {
    width: 40%;
  }
}